@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width:2000px;
  margin:0 auto;
  position: relative;
  padding-bottom: 2rem;
  /* min-height: 102vh; */
  min-width:480px;
  font-size: 14px;
}

body.modal-shown{
  overflow-y: hidden;
}

.modal{
    position: fixed;
    inset:0;
    background: rgba(10,10,10,0.8);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    padding: 1rem;
    display: grid;
    place-items: center;
    z-index:999;
}

.modal-header{
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h1{
    font-size:18px;
    color: var(--primary-color);
    text-transform: uppercase;
}

.btn-close{
    padding: 4px 8px;
    border: none;
    cursor: pointer;
    color: var(--primary-color);
    background: #fff;
    border-radius: 4px;
}

.btn-close:hover{
    color: #fff;
    background: var(--hover-bg-color);
}
.modal-window{
    width: 100%;
    max-width: 600px;
    background-color: white;
    border-radius:4px;
    padding: 0.5rem 0;
    opacity: 0;
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
    transition: all 0.2s linear;
}

.modal-window.visible{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
.modal-content{
    padding: 1rem;
}

.modal-content .error-text{
    margin-bottom: 1rem;
    color: red;
    font-size: var(--font-size);
    text-align: left;
}

.form-edit .form-control{
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form-control label{
    color: var(--text-color);
    font-size: var(--font-size);
}
.form-control textarea, .form-edit select{
    width:80%;
    color: var(--text-color);
}

.form-control textarea{
    resize: none;
}

.modal-footer{
    height: 50px;
    padding: 0.5rem;
    padding-right:1rem;
}
.modal-footer .form-actions{
    display: flex;
    justify-content: flex-end;
    padding: 1
}

.btn.btn-cancel, .btn.btn-cancel:hover{
    color: var(--text-color);
    background: #fff;
    margin-left: 0.5em;
}
.btn-options{
    background: transparent;
    border:none;
    cursor: pointer;
    border-radius:50%;
    width:25px;
    height:25px;
    transition: background-color var(--duration) linear;
  }
  
 .btn-options:hover, .btn-options.active{
    background: var(--card-bg-color);
  }
  
  .btn-options i{
    color: var(--text-color);
  }

  .menu{
    position: relative;
    margin-left: auto;
  }
  
  .menu .menu-list{
    position:absolute;
    min-width: 150px;
    top: calc(100% + 5px);
    right: calc(100% + 5px);
    right:0;
    list-style: none;
    width: -webkit-max-content;
    width: max-content;
    border-radius: 2px;
   
    box-shadow: 2px 3px 8px var(--box-shadow-color);
    background: #fff;
    padding: 0.5rem 0;
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: top right;
            transform-origin: top right;
    transition: opacity var(--duration) ease-in, -webkit-transform var(--duration) ease-in;
    transition: transform var(--duration) ease-in, opacity var(--duration) ease-in;
    transition: transform var(--duration) ease-in, opacity var(--duration) ease-in, -webkit-transform var(--duration) ease-in; /*display cannot be transitioned, 
    so instead work with transform and opacity*/
  }
  
  .menu-list.show-menu{
    z-index:1;
    opacity:1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  @-webkit-keyframes grow-menu{
    from{
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }

    to{
      opacity:1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

  @keyframes grow-menu{
    from{
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }

    to{
      opacity:1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  .menu .menu-list li{
    padding: 2px 0;
  }
  
  .menu-list li a{
    display:block;
    padding: 6px 8px;
    color: var(--text-color);
    text-decoration: none;
  }
  
  .menu-list li:hover{
    background: var(--card-bg-color);
    outline:none;
  }

  .menu-list li:hover > a{
  outline: none;
  }

  .list-heading > span{
    display: block;
    padding: 6px 8px;
    color: var(--text-color);
  }

  .menu-list .list-heading:hover{
    background: #fff;
    color: var(--text-color)
  }

@media screen and (max-width : 768px) {
 .sort-by .inner-menu{
    right: calc(100% + 2px);
    left: auto;
  }
}
  

.task-item{
    background-color: #fff;
    padding: 0.5rem;
    border-radius: 4px;
    line-height: var(--card-line-height);
    margin-bottom: 0.7rem;
    box-shadow: var(--box-shadow);
  }

.task-item:hover{
    cursor: grab;
}

.task-item.dragging{
  box-shadow: 5px 5px 5px var(--box-shadow-color);
}
  
  .task-item-body{
    height: calc(var(--card-line-height) * 4);
    overflow: hidden;
    margin: 4px 0;
  }
  
  .task-item-header{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4px;
    padding-bottom: 4px;
    border-bottom: 1px solid var(--primary-color);  
  }
  .task-item-body .task-title{
    color: var(--text-color);
    font-size: 14px;
    font-weight:400;
    margin-bottom: 8px;
    overflow-wrap: break-word;
  }
  
  .task-item-checkbox + label {
    height:15px;
    width:15px;
    cursor:pointer;
    border: 1px solid var(--primary-color);
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 0 2px var(--primary-color);
  }

  .task-item-checkbox:checked + label{
    background: var(--primary-color);
  }
  
  .task-item-checkbox{
    display: none;
  }  
  
  .task-item-footer{
    text-align:right;
  }

  .task-item-footer p{
    color: var(--text-color);
  }
  
  .task-item-footer .priority-container{
    display:inline-flex;
    align-items: center;
  }
  
  .priority-container .indicator{
    height:10px;
    width: 10px;
    border-radius: 50%;
    margin-right:4px;
  }

.task-category{
    min-height: 80vh;
    border-radius: 8px;
    padding: 0.5rem;
    padding-top: 2.5rem;
    position:relative;
    margin-top: 2rem;
    margin-bottom: 1rem;
    box-shadow: var(--box-shadow);
    background-color: var(--card-bg-color);
    transition: all 0.3s linear;
  }

  .task-category-body{
    transition: all 0.5s ease;
    -webkit-transform-origin: top center;
            transform-origin: top center;
  }
  
  .task-category.collapsed{
    height:0;
    min-height: 0;
  }

  .task-category.collapsed .task-category-body{
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  @media screen and (max-width : 768px){
    /* setting z indices so that menus won't be stacked under the next category in mobile mode*/
    #todo{
      z-index: 3;
    }
  
    #in-progress{
      z-index:2
    }
  
    #completed{
      z-index: 1
    }

  }

  .task-category .task-category-header{
    position: absolute;
    top:0;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width:80%;
    padding: 0.5rem 1rem;
    background-color: #fff;
    color: #fff;
    border-radius: 20px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    z-index:2;
    height: 40px;
    border: 1px solid var(--primary-color);
  }
  
  .task-category .task-category-header .count{
    position: absolute;
    left:0;
    top:0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: var(--primary-color);
    border-radius: 4px;
    padding: 2px 4px;
    min-width: 18px;
    text-align: center;
    margin: 0 auto 0 8px;
  }
  
  .task-category .task-category-header h1{
    font-size: 14px;
    font-weight:400;
    text-transform: uppercase;
    color: var(--text-color);
  }

  .task-category-header .btn-toggle{
    border: none;
    cursor: pointer;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    margin-left: 4px;
  }

  .task-category-header .btn-toggle i{
    color: var(--text-color);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  }

  .task-category-header .btn-toggle:hover{
    background-color: var(--primary-color);
  }

  .task-category-header .btn-toggle:hover i{
    color: #fff;
  }

  .task-category.collapsed .btn-toggle i{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  
.filter-container{
  text-align: center;
  padding: 1rem 0;
  background-color: var(--card-bg-color);
  box-shadow: 0 2px 5px var(--box-shadow-color);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  z-index: 900;
}

.filter-container .filter-form{
    width: 95%;
    margin: 0 auto;
}

.filter-container .search-task-input{
    width:80%;
    max-width:600px;
    margin-bottom: 0.5rem;
}

.filter-container .select{
    min-width:100px;
    width:8%;
    margin-right:0.5rem;
}

.filter-form .inline-controls{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.inline-controls > select{
    flex: 0 0 140px;
}

.inline-controls label{
    color: var(--text-color);
}
.nav-menu-container{
    position:fixed;
    height: 100vh;
    width: 100vw;
    top:0;
    left:0;
    z-index:999;
    background: rgba(0,0,0,0.8);
    display: flex;
    flex-direction:column;
  }
  
  .nav-menu-container .nav-menu{
    margin-top:auto;
    width: 100%;
    background: white;
    padding: 0.5rem 0 1.5rem;
    list-style: none;
    border-radius: 10px 10px 0 0;
    -webkit-transform:translateY(100%);
            transform:translateY(100%);
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  }
  
  .nav-menu-container .nav-menu.visible{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
  
  .nav-menu li, .nav-menu li > a{
    color: var(--text-color);
  }
  .nav-menu > li:first-child{
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .nav-menu li > a{
    padding: 0.5rem 1rem;
    display: block;
    text-decoration: none;
  }
  
  .nav-menu li:not(:first-child):hover{
    background-color: var(--card-bg-color);
  }
  

:root{
  --card-bg-color: #e4e4ff;
  --text-color: #593dae;
  --hover-bg-color: #5f409b;
  --card-line-height: 1.2rem;
  --primary-color: #865ac3;
  --box-shadow-color: #9992e6;
  --box-shadow: 0px 2px 5px var(--box-shadow-color);
  --duration: 0.1s;
}
.header {
  background: #fff;
  height:50px;
  border-bottom: 1px solid #865ac3;
  border-bottom: 1px solid var(--primary-color);
  position: -webkit-sticky;
  position: sticky;
  top:0;
  z-index: 990;
}

.header .content{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;

}

.header .app-title{
  font-size: 28px;
  color: #865ac3;
  color: var(--primary-color);
  line-height: 50px;
  font-weight:500;
  text-transform: uppercase;
  font-weight: 700;
  word-spacing: 0.2ch;
}

.tasks-container{
  width: 90%;
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 1rem;
}

.tasks-grid-container{
  width:100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(0,1fr));
  grid-gap: 0.7rem;
  gap: 0.7rem;
}

/* common styles */

input, select, textarea{
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #865ac3;
  border: 1px solid var(--primary-color);
  color: #593dae;
  color: var(--text-color);
  outline:none;
  font-size: 14px;
}

input, select, .btn{
  height: 36px;
}

:is(input, select, textarea):focus{
  box-shadow: 0 0 8px #9992e6;
  box-shadow: 0 0 8px var(--box-shadow-color);
}

input::-webkit-input-placeholder{
  color: #865ac3;
  color: var(--primary-color);
}

input:-ms-input-placeholder{
  color: #865ac3;
  color: var(--primary-color);
}

input::placeholder{
  color: #865ac3;
  color: var(--primary-color);
}

textarea{
  font-family: "Roboto";
}

.btn{
  padding: 0.5em 1em;
  border-radius: 4px;
  background-color: #865ac3;
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: 1px solid #865ac3;
  border: 1px solid var(--primary-color);
  font-size: 14px;
}

.btn:hover{
  background: #5f409b;
  background: var(--hover-bg-color);
}

.btn.btn-add-task{
  text-transform: uppercase;
}

.divider{
  margin: 0.5rem 0;
  border: 0.5px solid #c9c9e8;
}

.loading{
  text-align: center;
  padding-top:3rem;
}

.loading i{
  font-size:32px;
  -webkit-animation: spin 1.5s linear infinite;
          animation: spin 1.5s linear infinite;
  color: #865ac3;
  color: var(--primary-color);
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg)
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg)
  }
}

.navbar{
  text-align : center;
  border:none;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 48px;
  right: 16px;
  z-index: 99;
  background: #5f409b;
  background: var(--hover-bg-color);
  border-radius: 50%;
  box-shadow: 0px 2px 5px #9992e6;
  box-shadow: var(--box-shadow);
  -webkit-animation: jiggle 1s linear 1;
          animation: jiggle 1s linear 1;
  cursor:pointer;
}

.navbar i {
  display:block;
  color: #fff;
  margin: 0 auto;
}

@-webkit-keyframes jiggle{
  0%, 25%, 40%, 45%, 60%, 100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  30%, 35%, 50%, 55%{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}

@keyframes jiggle{
  0%, 25%, 40%, 45%, 60%, 100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  30%, 35%, 50%, 55%{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}

.navbar li a{
  text-transform: uppercase;
  padding:8px 16px;
}

/* media queries */

@media screen and (max-width : 992px) {

  .tasks-grid-container{
    width:100%;
  }

  .tasks-container{
    width: 90%;
  }
}

@media screen and (max-width: 768px){

  .header .app-title{
    font-size: 24px;
  }

  .tasks-grid-container{
    grid-template-columns: 1fr;
  }

  .task-item-body{
    height: calc(var(--line-height) * 2);
  }
  .task-category{
    min-height: 120px;
  }
}



